import React from "react"
import { Provider } from "react-redux"
import { createStore as reduxCreateStore, applyMiddleware } from "redux"
import createSagaMiddleware from "redux-saga"
import reducers from "./rootReducer"
import saga from "../saga/rootSaga"

const sagaMiddleware = createSagaMiddleware()

const createAppStore = () => {
  const Store = reduxCreateStore(reducers, applyMiddleware(sagaMiddleware))
  // use the same saga middleware that you have enhanced your store with
  sagaMiddleware.run(saga)
  return Store
}

// sagaMiddleware.run(Saga)

export default ({ element }) => (
  <Provider store={createAppStore()}>{element}</Provider>
)
